<script>
export default {
  name: "SvgIconFault",
  props: {
    status: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  computed: {
    changeColor() {
      if (this.status == 1) {
        return "#6FCF97";
      } else {
        if (this.status == 0) return "#B7B7B7";
        else return "#EB5757";
      }
    },
  },
};
